@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@1,100&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.album-list {
  z-index: 0;
}

.animate {
  position: absolute;
  top: 20%;
  animation: slide-up 1s;
}

@keyframes slide-up {
  from {margin-top: 100%;}
  to {margin-top: 0%;}
}
.welcome {
  background: rgba(27, 24, 27, 0.9);
  z-index: 0;
  height: 17vh;
  display: flex;
  justify-content: left;
  align-items: flex-end;
}

.welcome h1 {
  margin-top: 0px;
  margin-bottom: 1vh;
  margin-left: 1vh;
  font-size: 4rem;
  font-family: 'Roboto', sans-serif;
  color: #d9e00b;
  font-weight: 100;
}
.fade-in {
  animation: fadeIn ease 2s;
}
  
@keyframes fadeIn {
0% {opacity:0;}
100% {opacity:1;}
}


.track {
    border: 1px solid red;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 5%;
    padding-right: 5%;
    margin-top: 5px;
    margin-bottom: 5px;
    margin-left: 20%;
    margin-right: 20%;
}

.text-center {
    display: flex;
    justify-content: center;
    align-items: center;
}

.back-btn-to-lib {
    width: 100%;
    height: 100%;
    padding-top: 0px;
}

.App {
  text-align: center;
}

.body {
  background: black;
}

.particles {
  background: #1b0726;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
}

.slider {
  position: absolute;
  width: 100%;
  top: 0;
  height: 100%;
  overflow: hidden;
  transition: all 1s;
}

.overlay {
  position: fixed;
  width: 100vw;
  background-color: aqua;
  opacity: 0.8;
  height: 100vh;
  z-index: 2;
}

.hide {
  margin-top: 100vh;
  transition: margin 1s;
}

.show {
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-y: scroll;
  margin-top: 0vh;
  animation: slide-up 1s;
}

@keyframes slide-up {
  from {margin-top: 100vh;}
  to {margin-top: 0vh;}
}


